<template>
  <div class="container">
    <div class="col-sm-10">
      <counter :completed_cnt="completed" :incompleted_cnt="incompleted"></counter>
      <h1>Задачи</h1>
      <confirmation :message="message" v-if="showConfirmation"></confirmation>
      <button type="button" id="task-add" class="btn btn-success btn-sm align-left d-block" v-b-modal.todo-modal>Добавить задачу</button>


      <table class="table table-dark table-stripped table-hover">
        <thead class="thead-light">
          <tr>
            <th>Uid</th>
            <th>Описание</th>
            <th>Выполнена?</th>
            <th></th>
          </tr>
        </thead>

        <tbody>
          <tr v-for="(todo, index) in todos" :key="index">
            <td class="todo-uid">{{ todo.uid }}</td>
            <td>{{ todo.description }}</td>
            <td>
              <span v-if="todo.is_completed">Выполнено</span>
              <span v-else>Не выполнено</span>
            </td>
            <td>
              <div class="btn-group" role="group">
                <button type="button" class="btn btn-secondary btn-sm"v-b-modal.todo-update-modal @click="updateTodo(todo)">Обновить</button>
                &nbsp;
                <button type="button" class="btn btn-danger btn-sm" @click="deleteTodo(todo)">X</button>
              </div>
            </td>
          </tr>
        </tbody>
      </table>

      <b-modal ref="addTodoModal"
        id="todo-modal"
        title="Добавить задачу"
        hide-footer>

        <b-form @submit="onSubmit" @reset="onReset" class="w-100">
          <b-form-group id="form-description-group"
                label="Описание:"
                label-for="form-description-input">
            <b-form-input id="form-description-input"
                  type="text"
                  v-model="addTodoForm.description"
                  required
                  placeholder="Завести задачу">
            </b-form-input>
          </b-form-group>

          <b-form-group id="form-complete-group">
            <b-form-checkbox-group v-model="addTodoForm.is_completed" id="form-checks">
              <b-form-checkbox value="true">Задача выполнена?</b-form-checkbox>
            </b-form-checkbox-group>
          </b-form-group>

          <b-button type="submit" variant="primary">Добавить</b-button>
          <b-button type="reset" variant="danger">Сброс</b-button>
        
        </b-form>
      
      </b-modal>

      <b-modal ref="updateTodoModal"
      id="todo-update-modal"
      title="Update"
      hide-footer>

        <b-form @submit="onUpdateSubmit" @reset="onUpdateReset" class="w=100">
          <b-form-group id="form-update-description-group"
          label="Описание:"
          label-for="form-update-description-input">
          <b-form-input id="form-update-description-input"
            type="text"
            v-model="updateTodoForm.description"
            required>            
          </b-form-input>
        </b-form-group>

        <b-form-group id="form-update-complete-group">
          <b-form-checkbox-group v-model="updateTodoForm.is_completed" id="form-update-checks">
            <b-form-checkbox value="true">Задача выполнена?</b-form-checkbox>
          </b-form-checkbox-group>
        </b-form-group>

        <b-button-group>
          <b-button type="submit" variant="primary"">Обновить</b-button>
          <b-button type="reset" variant="danger">Сброс</b-button>
        </b-button-group>

        </b-form>
      </b-modal>

    </div>
  </div>
</template>

<style>
button#task-add {
  margin-top: 20px;
  margin-bottom: 20px;
}
h1, td {
  text-align: left;
}
.todo-uid {
  text-align: right;
}
</style>

<script>
  import axios from 'axios';
  import Confirmation from "./Confirmation.vue";
  import Counter from "./Counter.vue";

  export default {
    name: "Todo",

    // Определяем данные
    data() {
      return {
        todos: [],

        // Объект содержит те же поля, что и в модальном окне
        addTodoForm: {
          description: "",
          is_completed: [],
        },
        updateTodoForm: {
          uid: 0,
          description: "",
          is_completed: [],
        },
        message: "",
        showConfirmation: false,
        completed: this.countTodos()[0],
        incompleted: this.countTodos()[1],
      };
    },

    methods: {

      getTodos() {
        let todoObjects = this.getLsData()[1];

        // this.todos = this.getLsData()[1];
        this.todos = todoObjects.sort(this.compare);
        this.setCounter()        
        },

        compare(a, b){
          const uidA = Number(a.uid);
          const uidB = Number(b.uid);
          let comparison = 0;

          if (uidA > uidB){
            comparison=1;
          }else if(uidA < uidB){
            comparison = -1;
          }
          return comparison
        },

      countTodos(){
        let completedTodo = 0;
        let incompletedTodo = 0;
        if(this.getLsData()[1].length!==0){
            for(let i=0; i<this.getLsData()[1].length; i++){
              let todo = this.getLsData()[1][i];
              if(todo["is_completed"]){
                completedTodo++;
              }else{
                incompletedTodo++;
              };
            };
          };
        return [completedTodo, incompletedTodo];
      },

      setCounter(){
        this.completed = this.countTodos()[0];
        this.incompleted = this.countTodos()[1];
      },

      resetForm() {
        this.addTodoForm.description = '';
        this.addTodoForm.is_completed = [];
        this.updateTodoForm.description = "",
        this.updateTodoForm.is_completed = [];
      },

      setLsTodo(descr, is_compl){
        let todoIndex;
        if(this.getLsData()[0].length==0){
          todoIndex = 0;
        }else{
          let maxIndex = Math.max.apply(null, this.getLsData()[0]);
          todoIndex = maxIndex + 1;}
        let todoObj = {uid: todoIndex, description: descr, is_completed: is_compl};
        localStorage.setItem(todoIndex, JSON.stringify(todoObj));
      },

      getLsTodo(index){
        localStorage.getItem(index);
      },

      getLsData(){
        const keys = Object.entries(localStorage);
        let todosIndicis = [];
        let todosObjects = [];
        for(let i=0; i<keys.length; i++){
          if(keys[i][1].match("description")){
            todosIndicis.push(Number(keys[i][0]));
            todosObjects.push(JSON.parse(keys[i][1]))
          }
        }
        return [todosIndicis, todosObjects];
      },

      onSubmit(event){
        // логика добавления задачи
        event.preventDefault();
        this.$refs.addTodoModal.hide();

        this.setLsTodo(this.addTodoForm.description, this.addTodoForm.is_completed[0]);

        this.message = `Задача "${this.addTodoForm.description}" добавлена.`;
        this.showConfirmation = true;
        this.resetForm();

        this.getTodos()
      },

      onReset(event){
        event.preventDefault();
        this.$refs.addTodoModal.hide();
        this.resetForm();
      },

      updateTodo(todo){
        this.updateTodoForm = todo;
      },

      onUpdateSubmit(event){
        event.preventDefault();
        this.$refs.updateTodoModal.hide();
        const updatedTodo = {uid: this.updateTodoForm.uid, description: this.updateTodoForm.description, is_completed: this.updateTodoForm.is_completed[0]};


        localStorage.setItem(this.updateTodoForm.uid, JSON.stringify(updatedTodo));

        this.getTodos();
        this.message=`Задача ${this.updateTodoForm.description} обновлена`;
        this.showConfirmation=true;
        },

      onUpdateReset(event){
        event.preventDefault();
        this.$refs.updateTodoModal.hide();
        this.resetForm();
      },
      deleteTodo(todo){
        localStorage.removeItem(todo.uid)
        this.getTodos();
        this.message="Задача удалена";
        this.showConfirmation=true;
      },
    },

    components: {
      confirmation: Confirmation,
      counter: Counter,
    },
    created() {
      this.getTodos();
    },
  };
</script>